<template>
  <div class="home-container">
    <van-nav-bar class="page-nav-bar" title="标题" fixed>
      <template #title>
        <van-button class="search-btn" round icon="search" type="info" to="/search">
          搜索</van-button
        >
      </template>
    </van-nav-bar>

    <van-tabs
      class="channel-tabs"
      v-model="active"
      animated
      swipeable
      swipe-threshold="2"
    >
      <van-tab v-for="obj in channels" :key="obj.id" :title="obj.name">
        <article-list :channel="obj"></article-list>
      </van-tab>

      <template #nav-right>
        <div class="placeholder"></div>
        <div class="hamburger-btn" @click="isChennelEditShow = true">
          <i class="toutiao toutiao-gengduo"></i>
        </div>
      </template>
    </van-tabs>
    <!-- 频道编辑 -->
    <van-popup
      class="edit-channel-popup"
      v-model="isChennelEditShow"
      position="bottom"
      :style="{ height: '100%' }"
      closeable
      close-icon-position="top-left"
    >
      <channel-edit
        :myChannels="channels"
        :active="active"
        :allChannels="allChannels"
        @update-active="onUpdateActive"
      ></channel-edit>
    </van-popup>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getUserChannelsAPI, getAllChannelsAPI } from '../../api/index.js'
import ArticleList from './components/article-list.vue'
import ChannelEdit from './components/channel-edit.vue'
import { getItem } from '../../utils/storage.js'
export default {

  name: 'homeIndex',
  components: {
    ArticleList,
    ChannelEdit
  },
  data () {
    return {
      active: 0,
      channels: [],
      isChennelEditShow: false,
      allChannels: []
    }
  },
  computed: {
    ...mapState(['user'])
  },
  created () {
    this.loadChannels()
    this.loadAllChannels()
  },

  methods: {
    async loadChannels () {
      try {
        let channelsTemp = []
        const localChannels = getItem('TOUTIAO_CHANNELS')
        if (this.user || !localChannels) {
          const { data: res } = await getUserChannelsAPI()
          channelsTemp = res.data.channels
        } else {
          channelsTemp = localChannels
        }
        this.channels = channelsTemp
      } catch (error) {
        this.$toast('获取频道列表数据失败')
      }
    },
    async loadAllChannels () {
      try {
        const { data: res } = await getAllChannelsAPI()
        this.allChannels = res.data.channels
      } catch (error) {
        this.$toast('获取全部频道列表数据失败')
      }
    },
    onUpdateActive (index, isShow = false) {
      this.active = index
      this.isChennelEditShow = isShow
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  padding-top: 174px;
  padding-bottom: 100px;
  /deep/.van-nav-bar__title {
    max-width: unset;
  }
  .search-btn {
    background-color: #5babfb;
    border: none;
    width: 555px;
    height: 64px;
    font-size: 28px;
    .van-icon {
      color: #fff;
      font-size: 28px;
    }
  }
  :deep(.channel-tabs) {
    .van-tabs__wrap {
      height: 82px;
      position: fixed;
      top: 92px;
      z-index: 1;
      left: 0;
      right: 0;
    }
    // .van-tabs__content {
    //   position: fixed;
    //   top: 174px;
    //   width: 100%;
    // }
    .van-tab {
      border-right: 1px solid #edeff3;
      min-width: 200px;
      font-size: 30px;
      color: #777;
    }
    .van-tab--active {
      color: #333;
      font-weight: 700;
    }
    .van-tabs__line {
      width: 32px;
      background-color: #5babfb;
    }
    .placeholder {
      width: 66px;
      flex-shrink: 0;
    }
    .hamburger-btn {
      position: fixed;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 66px;
      height: 82px;
      background-color: rgba(255, 255, 255, 0.9);

      &:before {
        content: "";
        position: absolute;
        left: 0;
        width: 2px;
        height: 58px;
        background-image: url(../../assets/gradient-gray-line.png);
        background-size: contain;
      }
    }
  }
  .edit-channel-popup {
    padding-top: 100px;
    box-sizing: border-box;
  }
}
</style>
