<template>
  <div class="article-list">
    <van-pull-refresh
      v-model="refreshing"
      :success-text="refreshingTxt"
      @refresh="onRefresh"
      success-duration="1500"
    >
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        :error.sync="error"
        error-text="请求失败，请点击重新加载"
        >
        <article-item v-for="(item,index) in list" :key="index" :article="item">

        </article-item>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import ArticleItem from '../../../components/article-item.vue'
import { getArticlesAPI } from '../../../api/index.js'
export default {
  name: 'ArticleList',
  methods: {
    async onLoad () {
      try {
        const { data: res } = await getArticlesAPI({
          channel_id: this.channel.id,
          timestamp: this.timesTamp || Date.now()
        })
        console.log(res)
        this.list.push(...res.data.results)
        this.loading = false
        if (res.data.results.length === 0) {
          this.finished = true
        } else {
          this.timesTamp = res.data.pre_timestamp
        }
      } catch (err) {
        this.error = true
        this.loading = false
        this.$toast('请求数据失败')
      }
    },
    async onRefresh () {
      try {
        const { data: res } = await getArticlesAPI({
          channel_id: this.channel.id,
          timestamp: Date.now()
        })
        this.list.unshift(...res.data.results)
        this.refreshing = false
        this.refreshingTxt = `刷新成功，增加了${res.data.results.length}条数据`
      } catch (error) {
        this.refreshingTxt = '刷新失败，请重试'
        this.refreshing = false
      }
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      timesTamp: 0,
      error: false,
      refreshing: false,
      refreshingTxt: '更新成功'
    }
  },
  props: {
    channel: {
      type: Object,
      default: () => ({})
    }
  },
  components: {
    ArticleItem
  }
}
</script>

<style scoped lang="less">
.article-list{
  overflow-y: auto;
  height: 79vh;
}</style>
