    <van-cell :to="`/article/${article.art_id}`">
      <template #title>
        <div class="title van-multi-ellipsis--l2">
          {{ article.title }}
        </div>
      </template>
      <template #label>
        <div v-if="article.cover.type === 3" class="cover-wrap">
          <div
