<template>
  <div class="channel-edit">
    <van-cell title="我的频道">
      <template #default>
        <van-button
          size="mini"
          round
          type="danger"
          plain
          class="edit-btn"
          @click="isEditShow = !isEditShow"
        >
          {{ isEditShow ? "完成" : "编辑" }}
        </van-button>
      </template>
    </van-cell>

    <van-grid :gutter="10" class="my-grid">
      <van-grid-item
        class="channel-item"
        v-for="(obj, index) in myChannels"
        :key="obj.id"
        :text="obj.name"
        icon="close"
        :class="{ active: active === index }"
        @click="onMyChannel(obj, index)"
      >
        <template #icon>
          <van-icon name="close" v-show="isEditShow && index !== 0"></van-icon>
        </template>
      </van-grid-item>
    </van-grid>

    <van-cell :border="false">
      <div slot="title" class="title-text">频道推荐</div>
    </van-cell>

    <van-grid :gutter="10" class="recommend-grid">
      <van-grid-item
        class="channel-item"
        v-for="obj in recommendChannels_lodash"
        :key="obj.id"
        :text="obj.name"
        icon="plus"
        @click="onAddChannel(obj)"
      />
    </van-grid>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { setItem } from '../../../utils/storage.js'
import { addUserChannelsAPI, deleteUserChannelAPI } from '../../../api/index.js'
export default {

  data () {
    return {
      myChannelsClone: this.myChannels,
      isEditShow: false
    }
  },
  methods: {
    async onAddChannel (newChannel) {
      this.myChannelsClone.push(newChannel)

      if (this.user) {
        try {
          const channel = [
            {
              id: newChannel.id,
              seq: this.myChannels.length - 1
            }
          ]
          await addUserChannelsAPI({
            channels: channel
          })
          this.$toast('添加成功')
        } catch (err) {
          this.$toast('添加失败')
        }
      } else {
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    },
    onMyChannel (channel, index) {
      if (this.isEditShow) {
        if (index === 0) return
        // 编辑状态删除频道
        this.myChannelsClone.splice(index, 1)
        if (index <= this.active) {
          this.$emit('update-active', this.active - 1, true)
        }
        this.deleteChannel(channel)
      } else {
        this.$emit('update-active', index)
      }
    },
    async deleteChannel (delChannel) {
      if (this.user) {
        try {
          await deleteUserChannelAPI(delChannel.id)
        } catch (err) {
          this.$toast('删除频道失败，请稍后重试')
        }
      } else {
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    }
  },
  computed: {
    ...mapState(['user']),
    // recommendChannels () {
    //   const newList = this.allChannels.filter(channels => {
    //     return !this.myChannels.find(myChannels => {
    //       return myChannels.id === channels.id
    //     })
    //   })
    //   return newList
    // },
    // 第二种方法
    recommendChannels_lodash () {
      return this.$_.differenceBy(this.allChannels, this.myChannels, 'id')
    }

  },
  props: {
    myChannels: {
      type: Array,
      default: () => []
    },
    active: {
      type: Number,
      default: 0
    },
    allChannels: {
      type: Array,
      default: () => []
    }
  }
}
</script>

<style lang="less" scoped>
.channel-edit {
  .title-text {
    height: 100%;
    line-height: 58px;
    font-size: 32px;
    color: #333333;
  }

  .edit-btn {
    line-height: 48px;
    width: 104px;
    height: 48px;
    font-size: 26px;
    color: #f85959;
    border: 1px solid #f85959;
  }

  .channel-item {
    height: 86px;
    width: 160px;

    /* 设置按钮背景 */
    /deep/ .van-grid-item__content {
      background-color: #f5f5f6;

      /* 设置按钮字体样式 */
      .van-grid-item__text {
        color: #222;
        font-size: 28px;
        margin-top: 0;
      }
    }
  }
  /deep/ .my-grid {
    .van-grid-item__content {
      .van-grid-item__icon-wrapper {
        position: absolute;
        right: -10px;
        top: -10px;
        font-size: 30px;
        color: #cacaca;
        z-index: 2;
      }
    }
    .active {
      .van-grid-item__text {
        color: red;
      }
    }
  }
  /deep/ .recommend-grid {
    .van-grid-item__content {
      white-space: nowrap;
      flex-direction: row;
      .van-icon-plus {
        font-size: 28px;
        margin-right: 6px;
      }
    }
  }
}
</style>
